import { Injector } from '@angular/core';
import { ExpressionFormControlComponent, IExpressionFormControlOptions } from '@helix/platform/shared/components';
import {
  IViewActionDesignPropertyEditorConfig,
  IViewActionDesignProperties,
  IViewActionDesignSandbox,
  ViewActionDesignEditableProperties
} from '@helix/platform/view/api';
import { RxViewDesignerActionModel } from './view-designer-action-model.class';
import { take } from 'rxjs/operators';

export class RxViewDesignerDefaultActionModel extends RxViewDesignerActionModel {
  defaultProps: ViewActionDesignEditableProperties<
    IViewActionDesignProperties
  > = this.sandbox.descriptor.parameters.reduce((initialProps, param) => {
    initialProps[param.name] = param.defaultValue;

    return initialProps;
  }, {});

  constructor(protected injector: Injector, readonly sandbox: IViewActionDesignSandbox) {
    super(injector, sandbox);

    this.sandbox.actionProperties$.pipe(take(1)).subscribe((props) => {
      this.sandbox.updateActionProperties({
        ...this.defaultProps,
        ...props
      });

      this.sandbox.setActionPropertyEditorConfig(this.getActionEditorConfig());
    });
  }

  getActionEditorConfig(): IViewActionDesignPropertyEditorConfig {
    return this.sandbox.descriptor.parameters
      .filter((param) => param.editor)
      .map((param) => ({
        name: param.name,
        component: param.editor,
        options: {
          label: param.label,
          isRequired: param.isRequired,
          tooltip: param.tooltip,
          ...(param.editor === ExpressionFormControlComponent
            ? ({
                dataDictionary$: this.expressionConfigurator.getDataDictionary(param.name),
                operators: this.expressionConfigurator.getOperators(param.name)
              } as IExpressionFormControlOptions)
            : param.editorOptions || {})
        }
      }));
  }
}
